import * as React from "react"
import { Link } from "gatsby";

export default function Features({ list }) {
  if (!list?.length) {
    return null;
  }
  return (
    <ul className="hashtags">
      {list.map(f => (<li key={f}><Link to={`/#participants?feature=${f}`}>#{f}</Link></li>))}
    </ul>
  );
}